.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1{
  color: red;
  font-size: 6rem;
  text-align: center;
  top: 20%;
}

h2{
  color: red;
  font-size: 4rem;
  text-align: center;
  top: 20%;
}

@media (max-width : 100px){
  h1{
    color: red;
    font-size: 2rem;
    text-align: center;
    top: 20%;
  }
  h2{
    color: red;
    font-size: 1rem;
    text-align: center;
    top: 20%;
  }
}

@media (max-width : 200px){
  h1{
    color: red;
    font-size: 3rem;
    text-align: center;
    top: 20%;
  }
  h2{
    color: red;
    font-size: 2rem;
    text-align: center;
    top: 20%;
  }
}

@media (max-width : 300px){
  h1{
    color: red;
    font-size: 3rem;
    text-align: center;
    top: 20%;
  }
  h2{
    color: red;
    font-size: 2rem;
    text-align: center;
    top: 20%;
  }
}
